import React, { useContext } from 'react';
import { ProductContext } from '../contextAPI/ProductContext';
import '../css/Product.css';
const ProductsPage = () => {
  const { products } = useContext(ProductContext);

  return (
    <>
    <div className='shop-product-title'>
      <h2>Trending Products</h2>
    </div>
    <div className='product-row'>
      {products.length > 0 ? (
        <ul className='product-box'>
          {products.map((product) => (
            <li key={product.id}>
              {/* {product.name} - ${product.price} - {product.description} - {product.category} - {product.productimage} */}
              <div className='product-wrapper'>
                <div class="product-container">
                  <img className="unsplash-tk-fsk-jy-8-pq-8g" src={product.productimage}/>
                  <div class="product-actions">
                    <div class="like">
                      <i class="icn-favorite fa-regular fa-heart"></i>
                    </div>
                    <div class="basket">
                      <i className="icn-favorite2 fa-solid fa-cart-shopping" />
                    </div>
                    <div class="like">
                      <i className="icn-favorite3 fa-solid fa-eye" />
                    </div>
                  </div>
                  <div class="tag">
                    <div class="sale">Sale</div>
                  </div>
                </div>
                
              </div>
              {/* Start of product details */}
              <div className="product-details-wrapper">
                <div className="product-category-box">
                    <div className="product-category">
                        <a href="" className="link-product-category">{product.category}</a>
                    </div>
                </div>
                <div className="product-title">{product.name}</div>
                <div className="product-description">{product.description}</div>
                <div className="prices-box">
                    <div className="discounted-price">N{product.discountedprice}</div>
                    <div className="sales-price">N{product.price}</div>
                </div>    
                <a href="" className="add-to-cart-btn">
                    <div className="cart-text">Add to Cart</div>
                </a>
              </div>
            </li>
          ))}
        </ul>
      ) : (
        <p>No products available</p>
      )} 
    </div>
    </>
  );
};

export default ProductsPage;
