import React, { useState } from 'react';
import '../css/MobileNav.css';
import { Link } from 'react-router-dom';

const MobileNav = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="mobile-nav">
      <button className="menu-btn" onClick={toggleMenu}>
        ☰
      </button>

      <div className={`nav-menu ${isOpen ? 'open' : ''}`}>
        <button className="close-btn" onClick={toggleMenu}>
          &times;
        </button>
        <ul>
          <li><Link to="/">Home</Link></li>
          <li><Link to="/pages/shop">Shop</Link></li>
          <li><Link to="/pages/wishlists">Wishlists</Link></li>
          <li><Link to="/pages/about">About Us</Link></li>
          <li><Link to="/pages/contact">Contact Us</Link></li>
          <li><Link to="/pages/myaccount">My Account</Link></li>
        </ul>
      </div>
    </div>
  );
};

export default MobileNav;
