import React, { createContext, useState } from 'react';
import productone from '../images/productone.svg';

// Create Product Context
export const ProductContext = createContext();

export const ProductProvider = ({ children }) => {
  // Initial product list state
  const [products, setProducts] = useState([
    { id: 1, name: 'Product 1', description: 'This is the first description', price: 100,discountedprice: 90, category: 'Mens Wear', productimage: productone },
    { id: 2, name: 'Product 2', description: 'This is the second description', price: 200,discountedprice: 190, category: 'Ladies Wear', productimage: productone },
    { id: 3, name: 'Product 3', description: 'This is the third description', price: 300, discountedprice: 290, category: 'Shoes', productimage: productone },
    { id: 4, name: 'Product 4', description: 'This is the forth description', price: 400, discountedprice: 390, category: 'Bags', productimage: productone },
    { id: 5, name: 'Product 5', description: 'This is the fifth description', price: 500, discountedprice: 490, category: 'Bags', productimage: productone },
    { id: 6, name: 'Product 6', description: 'This is the sixth description', price: 600, discountedprice: 590, category: 'Ladies Wear', productimage: productone },
  ]);

  return (
    <ProductContext.Provider value={{ products, setProducts }}>
      {children}
    </ProductContext.Provider>
  );
};
