
import React, { Helmet } from 'react-helmet';
import AccountMainHeader from '../components/AccountMainHeader';
import Topheader from '../components/TopHeader';

const MyAccountPage = () => {

  return (
    <>
    <Helmet>
      <title>Account</title>
    </Helmet>
    <Topheader />
    <AccountMainHeader />
    <h1>My Account</h1>
    <p>Welcome to your account dashboard. Here's your personal information:</p>
    <ul>
      <li>Name: John Doe</li>
      <li>Email: johndoe@example.com</li>
      <li>Account Type: User</li>
    </ul>
    <button>Edit Profile</button>
    <button>Change Password</button>
    </>
  );
};


export default MyAccountPage;
