import React from "react";
import { Helmet } from "react-helmet";
import Topheader from "../components/TopHeader";
import MainHeader from "../components/MainHeader";


function About() {
    return (
        <>
        <Helmet>
            <title>About Us - Sense Shopping</title>
        </Helmet>
        <Topheader />
        <MainHeader />
        </>
    )
}

export default About